
*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.drifter-layout {
  --panel-pad: 2rem;
  --aside-width: min(max(30vmax, 12rem), 100vmin);
  display: flex;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: trench, sans-serif;
  color: var(--foreground-color);
  background-color: var(--background-color);
}

.drifter-layout-main {
  overflow: hidden;
  flex-grow: 1;
}

.drifter-layout-sidebar {
  flex-shrink: 0;
  position: relative;
  width: 0;
  padding: var(--panel-pad);
  padding-right: 0;
  background-color: var(--background-color-complement);
  transition: all 444ms ease-in 44ms;
}

.drifter-layout-sidebar:before {
  content: attr(data-heading);
  position: absolute;
  top: .75rem;
  left: .75rem;
}

.drifter-layout-sidebar:hover {
  width: var(--aside-width);
}


.drifter-label {
  display: block;
  line-height: 1.5;
  white-space: nowrap;
}

.drifter-has-neumorphism {
  --neumorphism-bg-color: var(--background-color-complement);
  --neumorphism-shadow-color-tint: var(--background-color-tint);
  --neumorphism-shadow-color-shade: var(--background-color-shade);

  --neumorphism-shadow-offset: .0375rem;
  --neumorphism-shadow-blur: calc(var(--neumorphism-shadow-offset) * 10);
  --neumorphism-shadow-spread: calc(var(--neumorphism-shadow-offset) * -.5);

  background-color: var(--neumorphism-bg-color);
  box-shadow:
    calc(var(--neumorphism-shadow-offset) * -1)
    calc(var(--neumorphism-shadow-offset) * -1)
    var(--neumorphism-shadow-blur)
    var(--neumorphism-shadow-spread)
    var(--neumorphism-shadow-color-tint),
    var(--neumorphism-shadow-offset)
    var(--neumorphism-shadow-offset)
    var(--neumorphism-shadow-blur)
    var(--neumorphism-shadow-spread)
    var(--neumorphism-shadow-color-shade);
}


.drifter-select,
.drifter-select .drifter-select-option {
  height: var(--select-height);
  outline: none;
  border: 0 none;
  color: var(--accent-color);
}

.drifter-select {
  --select-height: 1.5rem;
  -webkit-padding-start: .5em;
          padding-inline-start: .5em;
  -webkit-padding-end: 0;
          padding-inline-end: 0;
  border-radius: calc(var(--select-height) * .5);
  font: bold 1rem trench, sans-serif;
  background-color: var(--background-color-complement);
}

.drifter-select:hover,
.drifter-select:focus {
  color: var(--accent-color-complement);
}


.drifter-input {
  --input-height: 1.5rem;
  height: var(--input-height);
  padding: 0;
  margin: 0;
  outline: none;
  border: 0 none;
  border-radius: calc(var(--input-height) * .5);
  font: bold 1rem trench, sans-serif;
  color: var(--accent-color);
  background-color: transparent;
}

.drifter-input:hover,
.drifter-input:focus {
  color: var(--accent-color-complement);
}


.drifter-number-input {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  -webkit-padding-start: .5rem;
          padding-inline-start: .5rem;
  -webkit-padding-end: 0;
          padding-inline-end: 0;
}

.drifter-settings {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 2.4rem .4rem;
}

.drifter-settings > * {
  -webkit-margin-after: .5rem;
          margin-block-end: .5rem;
}

.drifter-settings :not(:last-child) {
  -webkit-margin-end: 1.5rem;
          margin-inline-end: 1.5rem;
}

.drifter-settings .drifter-setting-reset-interval {
  white-space: nowrap;
}

.drifter-settings .drifter-setting-reset-interval :not(:last-child) {
  -webkit-margin-end: .1rem;
          margin-inline-end: .1rem;
}

@media (prefers-reduced-motion: no-preference) {

@-webkit-keyframes slideLeft {
  from { left: 0; right: 100%; }
  to { left: -100%; right: 0; }
}
@keyframes slideLeft {
  from { left: 0; right: 100%; }
  to { left: -100%; right: 0; }
}

}

/* make animations available only if the user enabled animations in the OS settings */


.drifter-sequencer {
  /* todo - get bpm from outside */
  --bpm: 99;
  --sequencer-ticks: 4;
  --sequencer-ticks-per-beat: 4;
  --sequencer-tape-speed: calc(
    ((60s / var(--bpm)) / var(--sequencer-ticks-per-beat)) * var(--sequencer-ticks)
  );
  /* --sequencer-tape-timing: linear; */
  --sequencer-tape-timing: steps(var(--sequencer-ticks));
  --channel-height: 1.2rem;
  display: flex;
  align-items: stretch;
  height: -webkit-min-content;
  height: min-content;
}

.drifter-sequencer-timeline {
  position: relative;
  overflow: hidden;
  flex-grow: 1;
}


.drifter-sequencer .drifter-sequencer-tape {
  /* dividing by 2 as the container width is 200% */
  --sequencer-tick-width: calc((100% / var(--sequencer-ticks) / 2));
  display: none;
  position: absolute;
  z-index: 9;
  top: 0;
  bottom: 0;
  left: 0;
  right: 100%;
  width: 200%;
  /* create a background image with a transparent stripe at the begining,
    and repeat it twice to span the full width */
  background-image: linear-gradient(
    90deg,
    transparent,
    transparent var(--sequencer-tick-width),
    var(--background-color) var(--sequencer-tick-width),
    var(--background-color) 50%,
    transparent 50%,
    transparent calc(50% + var(--sequencer-tick-width)),
    var(--background-color) calc(50% + var(--sequencer-tick-width)),
    var(--background-color) 100%
  );
  opacity: .75;
  pointer-events: none;
}

.drifter-sequencer.drifter-sequencer-running .drifter-sequencer-tape {
  display: block;
  animation:
    slideLeft
    var(--sequencer-tape-speed)
    var(--sequencer-tape-timing)
    reverse
    infinite;
}

.drifter-sequencer-channels {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.drifter-sequencer-channels .drifter-sequencer-channel {
  overflow: hidden;
  height: var(--channel-height);
  white-space: nowrap;
}

.drifter-sequencer-channel .drifter-sequencer-tick {
  -webkit-appearance: none;
          appearance: none;
  width: calc(100% / var(--sequencer-ticks));
  height: 100%;
  border-radius: calc(var(--channel-height) * .25);
  border: calc(var(--channel-height) * .1) solid var(--background-color);
  background-color: var(--background-color-complement);
  cursor: pointer;
}

.drifter-sequencer-channel .drifter-sequencer-tick:checked:active,
.drifter-sequencer-channel .drifter-sequencer-tick:hover:not(:active) {
  background-color: var(--accent-color-complement);
}

.drifter-sequencer-channel .drifter-sequencer-tick:active,
.drifter-sequencer-channel .drifter-sequencer-tick:checked:not(:active) {
  background-color: var(--accent-color);
}

@media (prefers-reduced-motion: no-preference) {

@-webkit-keyframes softBlink {
  0% { opacity: 1 }
  66% { opacity: .3 }
  100% { opacity: 1 }
}
@keyframes softBlink {
  0% { opacity: 1 }
  66% { opacity: .3 }
  100% { opacity: 1 }
}

}

/* make animations available only if the user enabled animations in the OS settings */


.drifter-transport-button {
  --transport-button-size: 3rem;
  --transport-icon-size: calc(var(--transport-button-size) * .333);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: var(--transport-button-size);
  height: var(--transport-button-size);
  -webkit-margin-end: var(--transport-icon-size);
          margin-inline-end: var(--transport-icon-size);
  outline: none;
  border: 0 none;
  border-radius: calc(var(--transport-button-size) * .5);
  background-color: var(--background-color-complement);
  cursor: pointer;
}

.drifter-transport-button:last-of-type {
  -webkit-margin-end: 0;
          margin-inline-end: 0;
}

.drifter-transport-button:after {
  --transport-button-icon-color: var(--background-color);
  content: '';
  display: block;
  width: var(--transport-icon-size);
  height: var(--transport-icon-size);
  border-style: solid;
  /* faux icon fill color */
  border-color: var(--transport-button-icon-color);
}

.drifter-transport-button:hover:after {
  --transport-button-icon-color: var(--accent-color-complement);
}

.drifter-transport-button.drifter-transport-button-active:after {
  --transport-button-icon-color: var(--accent-color);
  -webkit-animation: softBlink 1.5s ease-in infinite;
          animation: softBlink 1.5s ease-in infinite;
}

.drifter-transport-button.drifter-transport-button-type-play:after {
  --transport-icon-size-play: calc(var(--transport-icon-size) * 1.1);
  width: var(--transport-icon-size-play);
  height: var(--transport-icon-size-play);
  margin-right: -20%;
  border-width: calc(var(--transport-icon-size-play) * .5)
                calc(var(--transport-icon-size-play) * .2)
                calc(var(--transport-icon-size-play) * .5)
                calc(var(--transport-icon-size-play) * .8);
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
}

.drifter-transport-button.drifter-transport-button-type-stop:after {
  --transport-icon-size-stop: calc(var(--transport-icon-size) * .9);
  width: var(--transport-icon-size-stop);
  height: var(--transport-icon-size-stop);
  background-color: var(--transport-button-icon-color);
}

.drifter-transport-button.drifter-transport-button-type-record:after {
  border-radius: calc(var(--transport-icon-size) * .5);
  border-width: calc(var(--transport-icon-size) * .5);
}

.drifter-transport {
  display: flex;
  align-items: stretch;
