.drifter-visualizations .drifter-visualization-tight {
}

.drifter-visualizations .drifter-visualization-loose {
}


.drifter-main-view {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: calc(100% - (var(--panel-pad) * 2));
  margin: var(--panel-pad);
}

.drifter-main-view > :not(:last-child) {
  -webkit-margin-after: .75rem;
          margin-block-end: .75rem;
}

.drifter-main-view-title {
  margin: 0;
  font-size: 15vmin;
  letter-spacing: -.05em;
  font-weight: 100;
  text-align: right;
  color: var(--accent-color);
